import { IResourceDictionary } from "pixi.js"
import { MapBase } from "../../engine/maps/map-base"
import { Dialogbox } from "../objects"

/**
 *
 */
export class CutScene extends MapBase {
  dialogBox

  constructor(
    resources: IResourceDictionary,
    width: number,
    height: number
  ) {
    super(resources, width, "maps.maension")

    this.withHud = false
    this.withCrosshair = false
    this.withPlayer = false
    this.withMouse = false

    this.dialogBox = new Dialogbox(resources)

    this.dialogBox.x = width / 2 - this.dialogBox.width / 2
    this.dialogBox.y =
      height / 2 - this.dialogBox.height / 2

    this.foregroundContainer.addChild(this.dialogBox)

    this.dialogBox.start()
  }

  set onDone(done: () => void) {
    this.dialogBox.onDone = done
  }

  public tick(delta: number) {}
}
